import React, {FunctionComponent} from 'react';
import Box from '@mui/material/Box';
import List from '@mui/material/List';
import Divider from '@mui/material/Divider';
import ListItem from '@mui/material/ListItem';
import ListItemIcon from '@mui/material/ListItemIcon';
import ListItemText from '@mui/material/ListItemText';
import InboxIcon from '@mui/icons-material/MoveToInbox';
import SwipeableDrawer from '@mui/material/SwipeableDrawer';
import Button from '@mui/material/Button';
import ListIcon from '@mui/icons-material/List';
import TW from "./assets/icons-twitter.png";
import DC from "./assets/icons-discord.png";
import IG from "./assets/icons-instagram.png";
import Home from "./assets/icons-home.png";
import { Link } from "react-router-dom";
import GroupIcon from '@mui/icons-material/Group';
import HomeIcon from '@mui/icons-material/Home';
import MapIcon from '@mui/icons-material/Map';
import SchoolIcon from '@mui/icons-material/School';
import QuestionMarkIcon from '@mui/icons-material/QuestionMark';

const Navigation: FunctionComponent = ()=>{

    type Anchor = 'top' | 'left' | 'bottom' | 'right';

        const [state, setState] = React.useState({
            top: false,
            left: false,
            bottom: false,
            right: false,
        });

        const toggleDrawer =
            (anchor: Anchor, open: boolean) =>
                (event: React.KeyboardEvent | React.MouseEvent) => {
                    if (
                        event &&
                        event.type === 'keydown' &&
                        ((event as React.KeyboardEvent).key === 'Tab' ||
                            (event as React.KeyboardEvent).key === 'Shift')
                    ) {
                        return;
                    }

                    setState({ ...state, [anchor]: open });
                };

        const list = (anchor: Anchor) => (
            <Box
                sx={{
                        width: anchor === 'top' || anchor === 'bottom' ? 'auto' : 250,
                    }}
                role="presentation"
                onClick={toggleDrawer(anchor, false)}
                onKeyDown={toggleDrawer(anchor, false)}
            >
                <List>
                    <ListItem button component={Link} to="/">
                        <ListItemIcon>
                             <HomeIcon />
                        </ListItemIcon>
                        <ListItemText primary={"Home"} disableTypography sx={{
                            fontFamily: "BaksoSapi"
                        }}/>
                    </ListItem>
                    <ListItem button component={Link} to="/gakuen">
                        <ListItemIcon>
                            <SchoolIcon />
                        </ListItemIcon>
                        <ListItemText primary={"Solana Gakuen"} disableTypography sx={{
                            fontFamily: "BaksoSapi"
                        }}/>
                    </ListItem>
                    <ListItem button component={Link} to="/roadmap">
                        <ListItemIcon>
                            <MapIcon />
                        </ListItemIcon>
                        <ListItemText primary={"Roadmap"} disableTypography sx={{
                            fontFamily: "BaksoSapi"
                        }}/>
                    </ListItem>
                    <ListItem button component={Link} to="/faq">
                        <ListItemIcon>
                            <QuestionMarkIcon />
                        </ListItemIcon>
                        <ListItemText primary={"FAQ"} disableTypography sx={{
                            fontFamily: "BaksoSapi"
                        }}/>
                    </ListItem>
                    <ListItem button component={Link} to="/schoolTeam">
                        <ListItemIcon>
                            <GroupIcon />
                        </ListItemIcon>
                        <ListItemText primary={"School Team"} disableTypography sx={{
                            fontFamily: "BaksoSapi"
                        }}/>
                    </ListItem>
                </List>
                <Divider />
                <List>
                    <ListItem>
                        <a href="https://twitter.com/SolanaGakuen" target="_blank">
                            <Button>
                                <img src={TW} alt={"twitter"} style={{
                                    height: "5vh",
                                    display: "block",
                                    marginLeft: "auto",
                                    marginRight: "auto",
                            }}/>
                            </Button>
                        </a>
                        <a href="https://www.instagram.com/solana_gakuen" target="_blank">
                            <Button>
                                <img src={IG} alt={"instagram"} style={{
                                    height: "5vh",
                                    display: "block",
                                    marginLeft: "auto",
                                    marginRight: "auto",
                                }}/>
                            </Button>
                        </a>
                        <a href="http://discord.gg/EhdeGYyYx2" target="_blank">
                            <Button>
                                <img src={DC} alt={"discord"} style={{
                                    height: "5vh",
                                    display: "block",
                                    marginLeft: "auto",
                                    marginRight: "auto",
                                }}/>
                            </Button>
                        </a>
                    </ListItem>
                </List>
            </Box>
        );

        return (
            <div style={{
                position: "absolute"
            }}>
                {(['left'] as const).map((anchor) => (
                    <React.Fragment key={anchor}>
                        <Button onClick={toggleDrawer(anchor, true)}><ListIcon sx={{
                            position: "fixed",
                            left: "10px",
                            top: "10px",
                            zIndex: "1000",
                            fontSize: "50px",
                            color: "black"
                        }}/></Button>
                        <SwipeableDrawer
                            anchor={anchor}
                            open={state[anchor]}
                            onClose={toggleDrawer(anchor, false)}
                            onOpen={toggleDrawer(anchor, true)}
                        >
                            {list(anchor)}
                        </SwipeableDrawer>
                    </React.Fragment>
                ))}
            </div>
        );
}

export default Navigation;